.board-container {
    margin: auto;
    margin-top: rem(40px);
    max-width: rem(1266px);
    padding: 20px 0 0;
    grid-template-columns: minmax(20px, 1fr) minmax(auto, 960px) minmax(20px, 1fr);
    .board-workspace-title {
        margin: rem(20px) 0 20px 0;
        svg {
            margin-inline-end: rem(15px);
            width: rem(35px);
            height: rem(35px);
        }
    }
    .stared-workspace {
        .stared-workspace-title {
            margin: 50px 0 20px 0;
            svg {
                margin-inline-end: rem(15px);
                width: rem(35px);
                height: rem(35px);
            }
        }
    }
    .board {
        justify-content: center;
        border-radius: 5px;
        width: rem(284px);
        height: rem(80px);
        background-size: cover;
        background-position: center center;
    }
    .add-board-container {
        background-color: #a0a5ad;
    }
    p {
        font-weight: 700;
        color: white;
        text-shadow: 1px -1px 2px #000000;
        // text-transform: uppercase;
        font-size: 1.2rem;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
}

.board:hover {
    filter: brightness(85%);
}